body {
  background-color: lightgreen;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
}

.box {
  width: 700px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  padding-left: 10px;
  padding-right: 10px;
  border: 4px solid white;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
}

.box-left h1 {
  text-align: left;
  color: white;
  margin-top: 5px;
  margin-bottom: 5px;
  /* -webkit-text-stroke: 2px black; */
}

.box-right h1 {
  text-align: right;
  color: hotpink;
  margin-top: 5px;
  margin-bottom: 5px;
}

.rules {
  position: fixed;
  width: 140px;
  /* margin-top: 150px; */
  /* margin-left: 85%; */
  right: 40px;
  bottom: 40px;
  background-color: lightgreen;
  color: white;
  font-size: 16px;
  border: 4px solid white;
  border-radius: 10px;
  padding: 10px 20px;
}

.rules:hover {
  background-color: green;
  color: white;
}

.rules[disabled] {
  background-color: gray;
}

.rules[disabled]:hover {
  background-color: gray;
}

.rock {
  width: 40px;
  transform: rotate(-30deg);
  padding: 30px;
}

.rock-circle {
  position: relative;
  top: 150px;
  right: 10px;
  height: 100px;
  width: 100px;
  background-color: white;
  border: 15px solid red;
  border-radius: 50%;
  display: inline-block;
  opacity: 0.75;
}

.paper {
  width: 40px;
  transform: rotate(-10deg);
  padding: 30px;
}

.paper-circle {
  position: relative;
  top: 150px;
  left: 10px;
  height: 100px;
  width: 100px;
  background-color: white;
  border: 15px solid blue;
  border-radius: 50%;
  display: inline-block;
  opacity: 0.75;
}

.scissors {
  width: 40px;
  transform: rotate(30deg);
  padding: 30px;
}

.scissors-circle {
  position: absolute;
  top: 435px;
  left: 50%;
  transform: translateX(-50%);
  height: 100px;
  width: 100px;
  background-color: white;
  border: 15px solid gold;
  border-radius: 50%;
  opacity: 0.75;
}

.play {
  position: fixed;
  width: 180px;
  left: 40px;
  bottom: 40px;
  background-color: red;
  color: white;
  font-size: 22px;
  border: 4px solid white;
  border-radius: 10px;
  padding: 10px 20px;
}

.play:hover {
  background-color: crimson;
  color: white;
}

.play[disabled] {
  background-color: gray;
}

.play[disabled]:hover {
  background-color: gray;
}

.play-rock {
  position: fixed;
  width: 80px;
  height: 80px;
  left: 42%;
  bottom: 40px;
  background-color: cyan;
  border: 4px solid white;
  border-radius: 40px;
}

.play-rock img {
  width: 40px;
  transform: rotate(-30deg);
}

.play-rock:hover {
  background-color: white;
  border: 10px solid red;
}

.play-rock[disabled] {
  background-color: gray;
}

.play-rock[disabled]:hover {
  background-color: gray;
  border: 4px solid white;
}

.play-paper {
  position: fixed;
  bottom: 40px;
  left: 50%;
  width: 80px;
  height: 80px;
  background-color: lightcoral;
  border: 4px solid white;
  border-radius: 40px;
  /* cursor: pointer; */
}

.play-paper img {
  width: 40px;
  transform: rotate(-10deg);
}

.play-paper:hover {
  background-color: white;
  border: 10px solid red;
}

.play-paper[disabled] {
  background-color: gray;
}

.play-paper[disabled]:hover {
  background-color: gray;
  border: 4px solid white;
}

.play-scissors {
  position: fixed;
  width: 80px;
  height: 80px;
  bottom: 40px;
  left: 58%;
  background-color: lightsalmon;
  border: 4px solid white;
  border-radius: 40px;
}

.play-scissors img {
  width: 40px;
  transform: rotate(30deg);
}

.play-scissors:hover {
  background-color: white;
  border: 10px solid red;
}

.play-scissors[disabled] {
  background-color: gray;
}

.play-scissors[disabled]:hover {
  background-color: gray;
  border: 4px solid white;
}

.show-rock {
  position: fixed;
  top: 300px;
  right: 600px;
  width: 40px;
  transform: rotate(-90deg);
  opacity: 0.25;
}

.show-paper {
  position: fixed;
  top: 380px;
  right: 600px;
  width: 40px;
  transform: rotate(-90deg);
  opacity: 0.25;
}

.show-scissors {
  position: fixed;
  top: 460px;
  right: 600px;
  width: 40px;
  transform: rotate(-90deg);
  opacity: 0.25;
}

.show-opponent-rock {
  position: fixed;
  top: 300px;
  left: 600px;
  width: 40px;
  transform: rotate(90deg);
  opacity: 0.25;
}

.show-opponent-paper {
  position: fixed;
  top: 380px;
  left: 600px;
  width: 40px;
  transform: rotate(90deg);
  opacity: 0.25;
}

.show-opponent-scissors {
  position: fixed;
  top: 460px;
  left: 600px;
  width: 40px;
  transform: rotate(90deg);
  opacity: 0.25;
}

.show-move {
  position: fixed;
  top: 380px;
  right: 700px;
  width: 40px;
  transform: rotate(-90deg);
}

.show-opponent-move {
  position: fixed;
  top: 380px;
  left: 700px;
  width: 40px;
  transform: rotate(90deg);
}

.result-lose {
  font-size: 28px;
  font-style: italic;
  font-family: cursive;
  color: red;
}

.result-draw {
  font-size: 28px;
  font-style: italic;
  font-family: cursive;
  color: orange;
}

.result-win {
  font-size: 28px;
  font-style: italic;
  font-family: cursive;
  color: green;
}

.start-play-msg {
  font-size: 20px;
  color: yellow;
}

.your-avatar {
  position: fixed;
  top: 300px;
  right: 340px;
  width: 170px;
}

.foe-avatar {
  position: fixed;
  top: 300px;
  left: 340px;
  width: 170px;
}

.rules-list-container {
  width: 900px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  padding-left: 10px;
  padding-right: 10px;
  background-color: blue;
  border: 4px solid white;
  border-radius: 10px;
}

.rules-list {
  text-align: left;
  font-family: cursive;
  font-size: 28px;
  color: white;
  list-style-type: square;
}

.rules-close {
  margin-bottom: 10px;
  color: red;
  font-family: cursive;
  font-size: 20px;
  border: 4px solid white;
  border-radius: 10px;
  cursor: pointer;
}
